@-webkit-keyframes fadeInRight {
    0% {
       opacity: 0;
       transform: translateX(+100%);
    }
    100% {
       opacity: 1;
       transform: translateX(0);
    }
 }

@keyframes fadeInRight {
    0% {
       opacity: 0;
       transform: translateX(+100%);
    }
    100% {
       opacity: 1;
       transform: translateX(0);
    }
 } 

 @-webkit-keyframes fadeOutRight {
    0% {
       opacity: 1;
       transform: translateX(0);
    }
    100% {
       opacity: 0;
       transform: translateX(+100%);
    }
 } 

 @keyframes fadeOutRight {
    0% {
       opacity: 1;
